/* Tables
/* ------------------------- */

table {
	background-color: $table-bg;
	color: $table-font-color;
}
caption {
	padding-top: $table-cell-padding;
	padding-bottom: $table-cell-padding;
	text-align: left;
}
th {
	text-align: left;
}


// Baseline styles

.table {
	width: 100%;
	max-width: 100%;
	color: $table-font-color;

	// Cells
	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th,
			> td {
				padding: calc($table-cell-padding / 2);
				vertical-align: top;
				border-top: 1px solid $table-border-color;
				font-family: $font-family-accent;
				font-size: 10px;
				line-height: 1.42857143em;

				@media (min-width: 992px) {
					padding: $table-cell-padding 10px;
					font-size: 11px;
				}
			}
		}
	}

	// Cells
	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th,
			> td {
				font-weight: $table-font-weight;
			}
		}
	}
	// Bottom align for column headings
	> thead > tr > th {
		vertical-align: bottom;
		border-bottom: 1px solid $table-border-color;
		font-weight: 700;
		color: $table-thead-color;
		font-style: $table-thead-font-style;
		text-transform: uppercase;
	}
	> tbody > tr > th {
		color: $table-thead-color;
		font-weight: normal;
	}
	// Remove top border from thead by default
	> caption + thead,
	> colgroup + thead,
	> thead:first-child {
		> tr:first-child {
			> th,
			> td {
				border-top: 0;
			}
		}
	}
	// Account for multiple tbody instances
	> tbody + tbody {
		border-top: 2px solid $table-border-color;
	}
}

.table--lg {
	// Cells
	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th,
			> td {
				padding: calc($table-cell-padding-lg / 2);

				@media (min-width: 992px) {
					padding: $table-cell-padding-lg 12px;
				}
			}
		}
	}
}

.table-thead-color {
	> thead > tr > th {
		background-color: $table-thead-bg-color;
	}
}

.table-top-border {
	> thead:first-child > tr:first-child > th {
		border-top: 1px solid $table-border-color;
	}
}

.table {
	> tbody > tr.highlighted {
		background-color: $table-bg-active;
	}
}

td.highlight {
	color: $headings-color;
}

// Hover effect
.table-hover {

	> tbody > tr {
		transition: background-color 0.2s ease;

		&:hover {
			color: $table-font-color;
			background-color: $table-bg-hover;
		}
	}
}

// Table Bordered
.table-bordered {
	border: 1px solid $table-border-color;
	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th,
			> td {
				border: 1px solid $table-border-color;
			}
		}
	}
	> thead > tr {
		> th,
		> td {
			border-bottom-width: 1px;
		}
	}
}

.table-wrap-bordered {
	border: 1px solid $table-border-color;
}

.table-responsive {

	@media (max-width: 767px) {
		border: none;
		margin-bottom: 0;
	}
}

.table--no-border {

	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th,
			> td {
				border: none;
			}
		}
	}
}

// Centered
.table__cell-center {
	text-align: center;

	> thead > tr > th {
		text-align: center;

		&:first-child {
			text-align: left;
		}
	}
}


// Table Stats
.table-stats {
	> thead,
	> tbody,
	> tfoot {
		> tr {
			> th,
			> td {
				text-align: center;
				vertical-align: middle;

				&.text-left {
					text-align: left;
				}

				&.text-right {
					text-align: right;
				}
			}
		}
	}


	> thead > tr > th,
	> tbody > tr > td {

		&:first-child {

			@media (min-width: 992px) {
				padding-left: $card-horizontal-padding;
			}
		}

		&:last-child {

			@media (min-width: 992px) {
				padding-right: $card-horizontal-padding;
			}
		}

	}

	th.table-stats__subheading {
		text-align: left;
		font-weight: bold;
		font-style: $table-thead-font-style;
		padding-left: $card-horizontal-padding;
	}

	th.table-stats__subheading--highlight {
		box-shadow: inset 6px 0 0 $color-primary;
	}
	th.table-stats__subheading--highlight-info {
		box-shadow: inset 6px 0 0 $color-info;
	}

}


// Table Header (like Card Header)
.table-card-header {

	> tr {

		> td,
		> th {
			padding-top: 25px !important;
			padding-bottom: 25px !important;

			&.table-card-header__title {
				font-size: $card-header-font-size;
				line-height: 1.2em;
				letter-spacing: -0.02em;
				margin-bottom: 0;
				text-transform: $card-header-text-transform;
				font-style: $font-style-h4;
				position: relative;

				&::before {

					@if $template == 'hockey' {
						display: none;
					} @else {
						content:"";
						display: block;
						position: absolute;
						width: 6px;
						left: 0;
						top: 0;
						bottom: 0;
						background-color: $color-primary;
						border-radius: 3px 0 0 0 ;
					}
				}
			}
		}
	}
}

// Stiped
.table-striped {
	tbody tr:nth-of-type(odd) {
		background-color: $table-striped-bg;
	}
}


// Thead Small
.table-thead-sm {
	> thead {
		> tr {
			> th {
				@media (min-width: 992px) {
					font-size: 10px;
				}
			}
		}
	}
}
